.fa-envelope, .fa-linkedin-in, .fa-github, .fa-twitter, .fa-instagram, .fa-facebook-square {
  margin-top: -2.3%;
  font-size: 24px;
  width: 56px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);

  border-radius: 360px;
  -webkit-animation:colorchange 20s infinite alternate;
}

.fa-envelope:hover, .fa-linkedin-in:hover, .fa-github:hover, .fa-twitter:hover, .fa-instagram:hover, .fa-facebook-square:hover {
  box-shadow: 0 0px 0px rgba(0,0,0,0.25), 0 6px 10px rgba(0,0,0,0.22);
  -webkit-animation:colorchange 20s infinite alternate;
}

